.App {
  font-family: sans-serif;
  text-align: center;
  margin: 0px;
}
.nav {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 15px;
  margin: 0px;
  background-color: darkgray;
  font-size: 25px;
}
span:hover {
  border-bottom: 2px solid cornflowerblue;
}

.addlist {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 25px;
  font-size: 22px;
}
.add {
  border: 2px solid black;
  border-radius: 4px;
  padding: 5px;
}
form {
  display: flex;
  flex-flow: column wrap;
  border-radius: 10px;
  border: 2px solid black;
  padding: 15px;
  margin: 45px;
  width: 450px;
  position: absolute;
  top: 95px;
  left: 450px;
}
table.PerformanceTable {
  table-layout: fixed;
  width: 100%;
  border-collapse: separate;
  border-spacing: 15px;
}
table.PerformanceTable td.PerformanceCell {
  width: 275px;
}

